import { Link } from "react-router-dom";

export default function Home({ pokemons, addCart }) {
  return (
    <section className="container" style={{ borderTop: "1px solid #ccc" }}>
      <h1>Home</h1>
      <ul className="itemList">
        {pokemons.map((pokemon, i) => {
          return (
            <li className="item" key={i}>
              <img src={pokemon.img} alt="이미지" />
              <p>{pokemon.name}</p>
              <p>{pokemon.price.toLocaleString()}원</p>
              <button onClick={() => addCart(i)}>장바구니 추가</button>
              <p>
                <Link to="/cart">구매하기</Link>
              </p>
            </li>
          );
        })}
      </ul>
    </section>
  );
}
