import { Link } from "react-router-dom";
import { useState } from "react";

export default function Cart({ cart, deleteCart }) {
  console.log(cart);
  const [price, setPrice] = useState(0);

  return (
    <section className="container cart">
      <h1>Cart</h1>
      {cart.length > 0 ? (
        <Items cart={cart} deleteCart={deleteCart} />
      ) : (
        <p>장바구니가 비었습니다</p>
      )}
      <hr />
      <button className="btn primary">주문하기</button>
      <Link to="/" className="btn">
        계속 쇼핑하기
      </Link>
      <div className="totalPrice">
        <p>
          총 결제금액:
          <span>
            {/* (시작값, 현재값) */}
            {cart.reduce((acc, current) => {
              console.log("acc =", acc);
              return acc + current.price;
            }, 0)}
          </span>
        </p>
      </div>
    </section>
  );
}

function Items({ cart, deleteCart }) {
  console.log("Items cart = ", cart);
  return (
    <ul className="cartList">
      {cart.map((item, i) => {
        return (
          <li className="item" key={i}>
            <img src={item.img} alt="이미지" />
            <p>{item.name}</p>
            <p>{item.price.toLocaleString()}원</p>
            <button
              onClick={() => {
                deleteCart(i);
              }}
            >
              삭제
            </button>
          </li>
        );
      })}
    </ul>
  );
}
