import { Routes, Route, Link } from "react-router-dom";
import "./styles.css";
import { data } from "./data";
import Home from "./pages/Home";
import Cart from "./pages/Cart";
import { useState, useEffect } from "react";

export default function App() {
  // console.log(data);
  const [pokemons, setPokemons] = useState(data);
  const [cart, setCart] = useState([]);
  const addCart = (id) => {
    // 선택한 상품 찾기
    let findItem = pokemons.find((pokemon, i) => {
      // console.log(i);
      return i === id;
    });

    // cart에 상품 추가
    let copy = [...cart];
    copy.push(findItem);
    //
    setCart(copy);
    // console.log("cart = ", cart);
  };

  // 카트 상품 삭제
  const deleteCart = (id) => {
    console.log("카트 삭제");
    // 카트에서 상품 제거
    let filterItem = cart.filter((pokemon, i) => {
      console.log(i);
      return i !== id;
    });

    // 배열 버퍼에 업데이트
    let copy = [...filterItem];

    // 카트 업데이트
    setCart(copy);
  };

  return (
    <>
      <nav className="container">
        <ul>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/cart">Cart</Link>
          </li>
        </ul>
      </nav>
      <Routes>
        <Route path="/" element={<Home pokemons={data} addCart={addCart} />} />
        <Route
          path="/cart"
          element={<Cart cart={cart} deleteCart={deleteCart} />}
        />
      </Routes>
    </>
  );
}
